<div class="page page-product"><div class="section section-main p-0 p-md-4"><div class="container-xl p-0"><div class="row product-main p-4 g-0"><div class="col-12 col-md-6 col-lg-5 pe-md-4 pe-lg-5 mb-4 mb-md-0 photos"><div class="main-photo"><div class="pic-l" v-html="getImg(currentImg)" @click="photoIsZoom=true"></div></div><div class="thumbs"><div class="pic-s" v-for="(img, id) in product.imgs" :class="{active: currentImg==img}" @click="setCurrentImg(img, id)"><div class="pic" :style="bgcss(img)"></div></div></div></div><div class="col-12 col-md-6 col-lg-7 product-main-info"><div class="pre-order-tag mb-2" v-if="product.state=='pre-order'"><span class="tag me-2">預購商品 </span><span>預計：{{ product.deliveryDate }} 出貨</span></div><div class="product-brand"><span>{{ product.brand }}</span></div><div class="product-title mb-5"><span>{{ product.title }}</span></div><div class="prices"><span class="unit">NT$</span><span class="num" v-if="product.price">{{ addCommaToNum(product.price) }}</span></div><div class="row specs g-0 mb-3"><label class="col-2">規格</label><ul class="col options p-0 m-0 d-flex"><li class="btn btn-outline-secondary spec-item me-2" v-for="spec in product.specs" @click="getCurrentSpec(spec)" :class="{active: currentSpec==spec}">{{ spec.name }}</li></ul></div><div class="row quantity g-0 mb-3"><label class="col-2">數量</label><div class="col d-flex"><div class="input-num"><div class="icon icon-btn icon-btn-l btn-sub" @click="quantitySub"><i class="fa-solid fa-minus"></i></div><input type="text" v-model="quantity"><div class="icon icon-btn icon-btn-l btn-add" @click="quantityAdd"><i class="fa-solid fa-plus"></i></div></div></div></div><div class="row btns mb-4 g-lg-2"><div class="col-12 col-lg-6"><div class="btn btn-lg btn-primary w-100 mb-2 mb-lg-0" @click="addCart"><i class="fa-solid fa-cart-shopping me-2"></i>加入購物車</div></div><div class="col-12 col-lg-6"><div class="btn btn-lg btn-outline-primary w-100"><i class="fa-regular fa-heart me-2"></i>收藏商品</div></div></div><ul class="notices ps-0"><p class="pre-order fw-bold">本商品為預購商品，{{ product.preorderDeadline }} 截止</p><p class="delivery">運送方式：一般宅配、國際快遞、與之前的預購訂單合併出貨、7-11取貨、全家超商取貨</p><p class="pay">付款方式：LINE Pay、PayPal、信用卡、ATM 虛擬帳號、超商代碼</p></ul></div></div><article class="product-content"><div class="otherInfo p-4 mt-4"><div class="title-area"><div class="title">購買注意事項</div><hr></div><div class="content"><p> <span>※ 請注意，付款完成才會受理訂單喔！只下單未付款將視為不需要此訂單</span></p><p> <span>※ 默認不含官盒，如須官盒請備註喔！</span></p><p><span>◎ </span><span>注意事項： </span></p><p class="list-item"><span>1. 工期個1-2月。</span></p><p class="list-item"><span>2. 由於個別螢幕之間可能會有色差，布布已經盡量放上各種圖片，請大家仔細評估後下單，不接受因為色差所提出的退貨。</span></p><p class="list-item"><span>3. 不限金額可零卡分期2-14期或中租分期，關於零卡分期的規定請看這邊。</span></p><p class="list-item"><span>4. 入訂後就不可以退款、不可以改規格，請確認好再下單呦！</span></p></div></div><div class="product-details p-4 mt-4"><div class="title-area"><div class="title">商品介紹</div><hr></div><div class="content"><p> <span>◎ </span><span>尺寸：三分/熊妹/普四/大六/普六</span></p><p class="list-item"><span>● </span><span>3分尺寸：三圍建議26cm/約20cm/26cm（sd10/13、dd、大部分3分女均可穿）</span></p><p class="list-item"><span>● </span><span>4分尺寸：三圍建議20cm/16cm/20cm（mdd、msd、狼兔體、大部分4分男女均可穿，瘦小身體可能撐不起慎拍）</span></p><p class="list-item"><span>● </span><span>6分尺寸：三圍建議15cm/13cm/17cm（yosd、大部分6分、大6分均可穿，瘦小身體可能撐不起慎拍）</span></p><p class="list-item"><span>● </span><span>熊尺寸：熊蛋打版，兔豆豆、4分巨嬰通穿</span></p><p class="list-item"><span>● </span><span>彈力至下往上穿,圖片均有色差,可能會色移,請清洗。</span></p><p> <span>◎ </span><span>全套點數：僅泳衣一件，無項圈。</span></p><p> <span>◎ </span><span>售價：全款255元。</span></p></div></div></article></div></div><Transition name="fadeIn" mode="out-in"><PhotoZoom v-if="photoIsZoom" :currentImg="currentImg" :imgs="product.imgs" :isOpen="photoIsZoom" :currentImgId="currentImgId" @update="updatePhotoZoom"></PhotoZoom></Transition><Transition name="fadeIn" mode="out-in"><ModalComp v-if="cartIsAdded" :msg="msg" :icon="msgIcon"><template v-slot:mask><MaskCover></MaskCover></template><template v-slot:msg></template></ModalComp></Transition></div>