<ModalComp class="photo-zoom-modal"><template v-slot:mask><MaskCover @click="isOpen=!isOpen"></MaskCover></template><template v-slot:header><div class="icon icon-btn icon-btn-l photo-zoom-modal_close" @click="isOpen=!isOpen"><i class="fa-solid fa-xmark"></i></div></template><template v-slot:content><div class="pic-wrap" v-html="getImg(imgUrl)" @mousedown="mouseDown($event)" @mouseup="mouseUp($event)"></div><div class="ctrl ctrl-l icon icon-l" @click="switchLeft"><i class="fa-solid fa-angle-left"></i></div><div class="ctrl ctrl-r icon icon-l" @click="switchRight"><i class="fa-solid fa-angle-right"></i></div></template></ModalComp>