<div class="products-list"><div class="section section-filters section-narrow py-0"><div class="container-xl p-0"><div class="options row g-0"><div class="col-12 col-md mb-2 filters"><span class="options-title">篩選：</span><div class="filter-item"><div class="filter-item_title" @click="filterStockIsOpen=!filterStockIsOpen;reactIsShow.stock=!reactIsShow.stock" :class="{active: filterStockIsOpen}"><span>供貨情況</span><i class="fa-solid fa-angle-down icon-s"></i></div><Transition name="fadeIn"><div class="filter-item_content" v-if="filterStockIsOpen"><div class="top"> <span class="content-title">篩選條件</span><span class="btn btn-text" @click="resetFilter('stock')">重設</span></div><div class="bottom"> <label>預購商品<input type="checkbox" name="pre-order" v-model="filters.isPreOrder"></label><label>現貨商品<input type="checkbox" name="spot-goods" v-model="filters.isSpot"></label></div></div></Transition></div><div class="filter-item"><div class="filter-item_title" @click="filterPriceIsOpen=!filterPriceIsOpen;reactIsShow.price=!reactIsShow.price" :class="{active: filterPriceIsOpen}"><span>價格</span><i class="fa-solid fa-angle-down icon-s"></i></div><Transition name="fadeIn"><div class="filter-item_content" v-if="filterPriceIsOpen"><div class="top"> <span class="content-title">金額</span><span class="btn btn-text" @click="resetFilter('price')">重設</span></div><div class="bottom"> <span>NT$</span><input type="text" name="min-price" v-model="filters.minPrice"><span>-</span><input type="text" name="max-price" v-model="filters.maxPrice"></div></div></Transition></div></div><div class="col-12 col-md-auto mb-2 sort"><span class="options-title">排序：</span><div class="sort-options d-flex"><div class="btn btn-sm btn-outline-primary" @click="sort='hot'" :class="{active: sort=='hot'}">最熱門</div><div class="select-wrap" @mouseover="sortPriceIsOpen=true" @mouseleave="sortPriceIsOpen=false"><div class="select-title btn btn-sm btn-outline-primary" :class="{active: sort=='ascending'||sort=='descending'}"><span>價格</span><i class="fa-solid fa-angle-down icon-s"></i></div><Transition name="fadeIn"><div class="select-options_wrap" v-show="sortPriceIsOpen"><div class="select-options_option" @click="sort='ascending'" :class="{active: sort=='ascending'}">價格: 低到高</div><div class="select-options_option" @click="sort='descending'" :class="{active: sort=='descending'}">價格: 高到低</div></div></Transition></div></div></div><div class="col-12 col-md-auto mb-2 product-num"><span>{{ sortProducts.length }} 件商品</span></div></div></div></div><div class="section section-main section-narrow"><div class="container-xl p-0"><Transition name="fadeIn" mode="out-in"><div class="row" v-if="products.length!=0" :key="productsInPage"><CardContainer v-for="(product, id) in productsInPage" :key="id" v-bind="product" @click="pageInit"></CardContainer></div></Transition><div class="notFound" v-if="productsInPage.length==0"><h2 class="title mt-5">沒有符合的商品。</h2></div><div class="pages-wrap"><ul class="pages m-0 p-0"><li class="icon icon-btn icon-btn-l" v-if="pagesNum&gt;1 &amp;&amp; currPage&gt;1" @click="goPrevPage"><i class="fa-solid fa-angle-left"></i></li><li class="icon icon-btn icon-btn-l page-count" v-for="i in pagesNum" :class="{active: currPage==i}" @click="goSpecificPage(i)"><span>{{ i }}</span></li><li class="icon icon-btn icon-btn-l" v-if="pagesNum&gt;1 &amp;&amp; currPage&lt;pagesNum" @click="goNextPage"><i class="fa-solid fa-angle-right"></i></li></ul></div></div></div><Transition><JsReact v-if="reactIsShow.stock" @click="filterStockIsOpen=!filterStockIsOpen;reactIsShow.stock=!reactIsShow.stock"></JsReact></Transition><Transition><JsReact v-if="reactIsShow.price" @click="filterPriceIsOpen=!filterPriceIsOpen;reactIsShow.price=!reactIsShow.price"></JsReact></Transition></div>