<div class="page page-home"><div class="test"></div><div class="carousel slide carousel-dark" id="banner" data-bs-ride="carousel"><div class="carousel-inner"><div class="carousel-item" v-for="(banner, bid) in banners" :class="{active: bid==0}"><router-link class="link" :to="getPath(banner.id)" v-html="getImg(banner.imgs)"></router-link></div></div><div class="carousel-control-prev" type="button" data-bs-target="#banner" data-bs-slide="prev"><span class="carousel-control-prev-icon" aria-hidden="true"></span></div><div class="carousel-control-next" type="button" data-bs-target="#banner" data-bs-slide="next"><span class="carousel-control-next-icon" aria-hidden="true"></span></div><div class="carousel-indicators"><button v-for="(i, id) in num" type="button" data-bs-target="#banner" :data-bs-slide-to="id" :class="{active: id==0}" :aria-current="{true: id==0}" :aria-label="getSlideId(id)"></button></div></div><section class="section scetion-limit section-narrow"><div class="container-xl"><div class="row"><div class="col"><h2 class="title mb-4">現正預購中</h2></div></div><div class="row"><CardContainer v-for="product in products" v-bind="product"></CardContainer></div></div></section></div>