<header class="header px-3 px-sm-4"><div class="top-header py-2"><div class="container-xl px-0"><div class="row g-0"><div class="col-3 col-lg d-flex align-items-center"><div class="icon icon-btn icon-btn-l d-lg-none nav-menu-btn" data-bs-toggle="offcanvas" data-bs-target="#nav-menu" aria-controls="offcanvasLabel"><i class="fa-solid fa-bars"></i></div><div class="search-bar-wrap d-flex align-items-center d-none d-lg-flex"><div class="icon icon-btn icon-btn-l search-icon" @click="searchIsActive=!searchIsActive" :class="{active: searchIsActive}"><i class="fa-solid fa-magnifying-glass"></i></div><input class="keyword-input" type="text" placeholder="商品搜尋" :class="{active: searchIsActive}" v-model="keywords" @change="goSearch"></div></div><div class="col-6 col-lg flex-center logo-wrap"><router-link class="logo" to="/">Crystal Apple</router-link></div><div class="col-3 col-lg d-flex align-items-center justify-content-end icons-wrap"> <div class="icon icon-btn icon-btn-l ms-1 ms-md-2 ms-lg-3"><i class="fa-solid fa-user"></i></div><div class="icon icon-btn icon-btn-l ms-1 ms-md-2 ms-lg-3 d-none d-sm-block"><i class="fa-regular fa-heart"></i></div><div class="icon icon-btn icon-btn-l ms-1 ms-md-2 ms-lg-3 d-none d-lg-block"><i class="fa-solid fa-clipboard-list"></i></div><div class="icon icon-btn icon-btn-l ms-1 ms-md-2 ms-lg-3 btn-cart" data-bs-toggle="offcanvas" data-bs-target="#cart-list" aria-controls="offcanvasLabel"> <i class="fa-solid fa-cart-shopping"></i><div class="cart-count" v-if="cartList.length!=0">{{ cartList.length }}</div></div></div></div></div></div><nav class="navigation d-none d-lg-block"><div class="container-xl px-0"><div class="nav-first d-flex align-items-center justify-content-between"><div class="nav-first-li flex-grow-1" v-for="first in navigation" @mouseover="first.isOpen = true" @mouseleave="first.isOpen = false" :class="{isOpen: first.isOpen}"><template v-if="!first.navSecond"><router-link class="nav-first-title py-3" :to="getPath(first,'')"><span>{{ first.navFirst }}</span></router-link></template><template v-if="first.navSecond"><div class="nav-first-title py-3"><span>{{ first.navFirst }}</span></div></template><transition name="fadeIn"></transition><div class="nav-second" v-if="first.navSecond" v-show="first.isOpen"><router-link class="topic-link" v-for="second in first.navSecond" :to="getPath(first, second)">{{ second }}</router-link></div></div></div></div></nav><nav class="offcanvas offcanvas-start navigation-menu" tabindex="-1" id="nav-menu" aria-labelledby="offcanvasLabel"><div class="offcanvas-header"><div class="search-bar-wrap d-flex py-2"><div class="icon icon-btn icon-btn-l search-icon me-2"><i class="fa-solid fa-magnifying-glass"></i></div><input class="keyword-input flex-fill" type="text" placeholder="商品搜尋" v-model="keywords" @change="goSearch"></div><div class="btn-close text-reset" type="button" data-bs-dismiss="offcanvas" aria-label="Close"></div></div><div class="offcanvas-body"><div class="nav-first"><div class="nav-first-li" v-for="first in navigation" @click="first.isOpen = !first.isOpen" :class="{isOpen: first.isOpen}"><template v-if="!first.navSecond"><router-link class="nav-first-title px-3" :to="getPath(first,'')" @click="navMenuIsOpen=!navMenuIsOpen"><span>{{ first.navFirst }}</span></router-link></template><template v-if="first.navSecond"><div class="nav-first-title px-3 d-flex justify-content-between align-items-center"><span>{{ first.navFirst }}</span><i class="fa-solid fa-angle-right icon-s"></i></div></template><div class="nav-second ps-3" v-if="first.navSecond" v-show="first.isOpen"> <router-link class="topic-link" v-for="second in first.navSecond" :to="getPath(first, second)" @click="navMenuIsOpen=!navMenuIsOpen">{{ second }}</router-link></div></div></div></div></nav></header>