<div class="modal_comp"><slot name="mask"></slot><div class="modal-wrapper flex-center"><div class="modal_header"><slot name="header"></slot></div><div class="modal-msg p-3 p-md-4" v-if="msg"><slot name="msg"><template v-if="icon=='success'"><div class="icon icon-l"><i class="fa-solid fa-circle-check success"></i></div></template><template v-if="icon=='error'"><div class="icon icon-l"><i class="fa-solid fa-circle-xmark error"></i></div></template><template v-if="icon=='notice'"><div class="icon icon-l"><i class="fa-solid fa-triangle-exclamation notice"></i></div></template><p class="m-0">{{msg}}</p></slot></div><div class="modal-content" v-else><slot name="content"></slot></div></div></div>