<div class="card-container col-6 col-sm-4 col-lg-3 mb-4 mb-lg-5 px-1 px-sm-2"><div class="link-top"><router-link :to="getPath(id)"><div class="pic" v-html="getImg(imgs.pic01,'')"></div></router-link><div class="btn-wrap"><div class="icon icon-btn icon-btn-l btn-heart" @click.stop="addWishList()" :class="{liked: isLiked}"><i class="fa-solid fa-heart"></i><i class="fa-regular fa-heart"></i></div></div><div class="tag-wrap"><span class="tag pre-order-tag" v-if="state=='pre-order'">預購</span></div></div><div class="info"><router-link class="link-bottom" :to="getPath(id)">{{ title }}</router-link><div class="details"> <a class="brand" href="#">{{ brand }}</a><div class="price"><span class="unit">NT$ </span><span class="num" v-if="price">{{ addCommaToNum(price) }}</span><span class="num" v-if="!price">{{ addCommaToNum(minPrice) }} ~ {{ addCommaToNum(maxPrice) }}</span></div></div></div><Transition name="fadeIn" mode="out-in"><ModalComp v-if="isAdded" :msg="msg" :icon="msgIcon"><template v-slot:mask><MaskCover></MaskCover></template><template v-slot:msg></template></ModalComp></Transition></div>