<div class="offcanvas offcanvas-end cart-list_wrap" tabindex="-1" id="cart-list" aria-labelledby="offcanvasLabel"><div class="offcanvas-header"><h5 class="offcanvas-title">我的購物車</h5><div class="btn-close text-reset" type="button" data-bs-dismiss="offcanvas" aria-label="Close"></div></div><div class="offcanvas-body cart_content"><div class="cart-list" v-if="cartList.length!=0"><div class="item p-2 mb-3" v-for="(item,id) in cartList"><div class="cart_thumbs me-1 me-sm-2"><div class="item-photo"><div class="pic" :style="bgcss(item.spec.url)"></div></div></div><div class="cart_info"><div class="pre-order"><div class="tag">預</div><span>預計 {{ item.deliveryDate }} 出貨</span></div><div class="brand">{{ item.brand }}</div><router-link class="product-title" @click="cartIsOpen=!cartIsOpen" :to="getPath(item.id)">{{ item.title }}</router-link><div class="spec"><span>{{ item.spec.name }}</span></div><div class="cart-quantity"><div class="input-num"><div class="icon icon-btn icon-btn-l btn-sub" @click="subQty(id,item.qty)"><i class="fa-solid fa-minus"></i></div><input type="text" :value="item.qty" @change="setQty(id,$event)"><div class="icon icon-btn icon-btn-l btn-add" @click="addQty(id,item.qty)"><i class="fa-solid fa-plus"></i></div></div></div><div class="subtotal">{{ addCommaToNum(item.price * item.qty)}}</div><div class="icon icon-btn icon-btn-l btn-delete" @click="deleteCartItem(id)"><i class="fa-regular fa-trash-can"></i></div></div></div></div><div class="empty-cart" v-if="cartList.length==0"><h4 class="subtitle">您的購物車是空的</h4></div><div class="btn btn-l btn-primary go_to_payment" v-if="cartList.length!=0">前往結賬</div></div></div>